/* eslint-disable eqeqeq */
/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */

import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useEffect, useState } from "react";
import Card from "../components/card";
import axios from "axios";

function MainPage(probs) {
  let [btnTime, setBtnTime] = useState(0);
  let [btnDisplay, setBtnDisplay] = useState("");
  let [loader, setLoader] = useState(false);

  useEffect(() => {
    if (btnTime == 2) {
      setBtnDisplay("none");
    }
  });
  return (
    <>
      <div
        className="main-bg"
        style={{
          backgroundImage: process.env.PUBLIC_URL + "url(/bg.png)",
        }}
      ></div>
      <Container>
        <Row>
          {probs.shoes.map((v, i, a) => {
            return <Card shoes={probs.shoes[i]} i={i + 1} />;
          })}
        </Row>
      </Container>
      <br />
      {loader === true ? (
        <span class="loader" style={{ display: loader }}></span>
      ) : null}

      <br />
      <Button
        style={{ display: btnDisplay }}
        variant="outline-dark"
        onClick={() => {
          if (btnTime == 0) {
            setLoader(true);
            axios
              .get("https://codingapple1.github.io/shop/data2.json")
              .then((result) => {
                let copy = [...probs.shoes, ...result.data];
                probs.setShoes(copy);
                setBtnTime(btnTime + 1);
                setLoader(false);
              })
              .catch(() => {
                console.log("실패함");
              });
          } else if (btnTime == 1) {
            setLoader(true);
            axios
              .get("https://codingapple1.github.io/shop/data3.json")
              .then((result) => {
                let copy = [...probs.shoes, ...result.data];
                probs.setShoes(copy);
                setBtnTime(btnTime + 1);
                setLoader(false);
              })
              .catch(() => {
                console.log("실패함");
                setLoader(false);
              });
          }
        }}
      >
        더보기
      </Button>
    </>
  );
}

export default MainPage;
