/* eslint-disable no-unused-vars */
import logo from "./logo.svg";
import "./App.css";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Suspense, createContext, lazy, useEffect, useState } from "react";
import data from "./data.js";
import {
  Link,
  Route,
  Routes,
  Outlet,
  useNavigate,
  json,
} from "react-router-dom";
import Card from "./components/card.js";
import MainPage from "./routes/MainPage.js";
import axios from "axios";
import { useQuery } from "react-query";
// import Detail from "./routes/Detail.js";
// import Cart from "./routes/Cart.js";
// lazy import
const Detail = lazy(() => import("./routes/Detail.js"));
const Cart = lazy(() => import("./routes/Cart.js"));

// context를 하나 만들어주는 역할.(context는 state 보관함으로 생각하면 쉬움.)
export let Context1 = createContext();

function App() {
  useEffect(() => {
    if (localStorage.getItem("watchedItem") === true) {
      return null;
    } else {
      const watchedItem = [];
      localStorage.setItem("watchedItem", JSON.stringify(watchedItem));
    }
  }, []);

  let [shoes, setShoes] = useState(data);

  let [stock, setStock] = useState([10, 11, 12]);

  // useNavigate() -> 페이지 이동을 도와줌.
  let navigate = useNavigate();

  // React Query 이용해서 ajax 요청.
  let result = useQuery(
    ["작명"],
    () => {
      return axios
        .get("https://codingapple1.github.io/userdata.json")
        .then((a) => {
          console.log("How many times does it refetch");
          return a.data;
        });
    },
    // 2000ms 안에는 Refetch 되지 않음
    { staleTime: 2000 }
  );

  // result.data -> ajax 요청이 성공했을 때 가져오는 데이터 반환.
  // result.isLoading -> ajax요청이 로딩중일때 true 반환
  // result.error -> ajax요청이 실패 했을 때 true 반환

  return (
    <div className="App">
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home">ShoesShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => {
                navigate("/");
              }}
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                navigate("/cart");
              }}
            >
              Cart
            </Nav.Link>
          </Nav>
        </Container>
        <Nav className="me-auto">
          {result.isLoading ? "로딩중" : result.data.name}
        </Nav>
      </Navbar>
      <Suspense fallback={<div>로딩중</div>}>
        <Routes>
          <Route
            path="/"
            element={<MainPage shoes={shoes} setShoes={setShoes}></MainPage>}
          />
          <Route
            path="/detail/:id"
            element={
              <Context1.Provider value={{ stock, shoes }}>
                <Detail shoes={shoes} setShoes={setShoes}></Detail>
              </Context1.Provider>
            }
          />

          {/* 장바구니 라우트 */}
          <Route path="/cart" element={<Cart></Cart>} />

          <Route path="/about" element={<About></About>}>
            <Route path="member" element={<div>멤버임</div>} />
            <Route path="location" element={<div>위치정보임</div>} />
          </Route>
          <Route path="/event" element={<Event></Event>}>
            <Route path="one" element={<p>첫 주문시 양배추 즙 서비스</p>} />
            <Route path="two" element={<p>생일 기념 쿠폰 받기</p>} />
          </Route>

          <Route path="*" element={<div>없는 페이지</div>} />
        </Routes>
      </Suspense>
    </div>
  );
}

function About() {
  return (
    <div>
      <h1>회사정보임</h1>
      <Outlet></Outlet>
    </div>
  );
}

function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  );
}

export default App;
