import { createSlice } from "@reduxjs/toolkit";

let cartData = createSlice({
  name: "cartData",
  initialState: [
    { id: 0, name: "White and Black", count: 2 },
    { id: 2, name: "Grey Yordan", count: 1 },
  ],
  reducers: {
    increaseAmount(state, action) {
      let found = state.findIndex((v, i, a) => {
        return v.id === action.payload;
      });
      state[found].count += 1;
    },

    decreaseAmount(state, action) {
      let found = state.findIndex((v, i, a) => {
        return v.id === action.payload;
      });
      state[found].count -= 1;
    },

    addItem(state, action) {
      let { id, title } = action.payload;

      let found = state.findIndex((v, i, a) => {
        return v.id === id;
      });

      if (found !== -1) {
        state[found].count += 1;
      } else if (found === -1) {
        let newData = {
          id: id,
          name: title,
          count: 1,
        };
        state.push(newData);
      }
    },

    deleteItem(state, action) {
      let copy = state.filter((v, i, a) => {
        return v.id !== action.payload;
      });
      return copy;
    },
  },
});

// .actions 하면 해당 state의 모든 reducer 함수들이 남음.
export let { increaseAmount, decreaseAmount, addItem, deleteItem } =
  cartData.actions;

export default cartData;
